import React, { useState } from "react";

import { Desktop, Mobile } from "../../../components/Responsive";

import userPhoto from "../../../static/img/user.png";

import {
  Wrapper,
  Title,
  LinkList,
  LinkItem,
  LinkTitle,
  UserPhoto,
  DeepLinkList,
  DeepLinkItem,
  Burger,
} from "./styled";
import BurgerContent from "./BurgerContent";

const links = [
  {
    title: "home",
    active: true,
    deepLinks: [
      { title: "world news", active: false },
      { title: "travel", active: false },
      { title: "technology", active: true },
      { title: "city", active: false },
      { title: "culture", active: false },
      { title: "more...", active: false },
    ],
  },
  { title: "discovery", active: false, deepLinks: null },
  { title: "photos", active: false, deepLinks: null },
  { title: "contact", active: false, deepLinks: null },
];

export default () => {
  const [burgerIsOpen, setBurgerIsOpen] = useState(false);

  const hadnleBurger = () => {
    if (burgerIsOpen) {
      document.body.style.overflow = "auto";
      setBurgerIsOpen(false);
    } else {
      document.body.style.overflow = "hidden";
      setBurgerIsOpen(true);
    }
  };
  return (
    <Wrapper>
      <Desktop>
        <Title>Logo</Title>
        <LinkList>
          {links.map((link, index) => (
            <LinkItem key={"link " + index}>
              <LinkTitle active={link.active}>{link.title}</LinkTitle>
              {link.deepLinks ? (
                <DeepLinkList>
                  {link.deepLinks.map((deepLink) => (
                    <DeepLinkItem active={deepLink.active}>
                      {deepLink.title}
                    </DeepLinkItem>
                  ))}
                </DeepLinkList>
              ) : (
                false
              )}
            </LinkItem>
          ))}
          <UserPhoto img={userPhoto} />
        </LinkList>
      </Desktop>
      <Mobile>
        <Burger onClick={hadnleBurger} />
        <Title>Logo</Title>
        <UserPhoto img={userPhoto} />
        <BurgerContent
          hadnleBurger={hadnleBurger}
          burgerIsOpen={burgerIsOpen}
        />
      </Mobile>
    </Wrapper>
  );
};
