import React, { useState } from "react";

import {
  Wrapper,
  MainBlock,
  Title,
  SubTitle,
  Paragraph,
  Button,
  SliderWrapper,
  SliderContent,
  SliderHeader,
  SliderTitle,
  SliderNav,
  Arrow,
  Post,
  PostList,
  PostTitle,
  PostSubtitle,
  PrevText,
  PostTime,
} from "./styled";

const posts = [
  {
    title: "travel",
    subtitle: "Article title",
    prevText:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
  },
  {
    title: "technology",
    subtitle: "Article title",
    prevText:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
  },
  {
    title: "travel 2",
    subtitle: "Article title",
    prevText:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
  },
  {
    title: "technology 2",
    subtitle: "Article title",
    prevText:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
  },
];

export default () => {
  const [lastSlide, setLastSlide] = useState(1);

  const handleNextSlides = () => {
    posts.length - 1 <= lastSlide
      ? setLastSlide(1)
      : setLastSlide(lastSlide + 2);
  };

  const handlePrevSlides = () => {
    lastSlide <= 1
      ? setLastSlide(posts.length - 1)
      : setLastSlide(lastSlide - 2);
  };

  return (
    <Wrapper>
      <MainBlock>
        <div>
          <Title>world news</Title>
          <SubTitle>Amazing places in America to visit.</SubTitle>
          <Paragraph>
            For some reason — this country, this city, this neighborhood, this
            particular street —  is the place you are living a majority of your
            life in.
          </Paragraph>
          <Button>learn more</Button>
        </div>
      </MainBlock>
      <SliderWrapper>
        <SliderContent>
          <SliderHeader>
            <SliderTitle>MORE NEWS</SliderTitle>
            <SliderNav>
              <Arrow left onClick={handlePrevSlides} />
              <Arrow onClick={handleNextSlides} />
            </SliderNav>
          </SliderHeader>
          <PostList>
            {posts.map((post, index) => (
              <Post
                display={index == lastSlide || lastSlide - index == 1}
                key={"post " + index}
              >
                <PostTitle>{post.title}</PostTitle>
                <PostSubtitle>{post.subtitle}</PostSubtitle>
                <PrevText>{post.prevText}</PrevText>
                <PostTime>{post.time}</PostTime>
              </Post>
            ))}
          </PostList>
        </SliderContent>
      </SliderWrapper>
    </Wrapper>
  );
};
