export default () => {
  const [lastBigSlide, setLastBigSlide] = useState(1);
  const [lastSmallSlide, setLastSmallSlide] = useState(2);

  const handleNextBigSlides = () => {
    bigPosts.length - 1 <= lastBigSlide
      ? setLastBigSlide(1)
      : setLastBigSlide(lastBigSlide + 2);
  };

  const handlePrevBigSlides = () => {
    lastBigSlide <= 1
      ? setLastBigSlide(bigPosts.length - 1)
      : setLastBigSlide(lastBigSlide - 2);
  };

  const handleNextSmallSlides = () => {
    smallPosts.length - 1 <= lastSmallSlide
      ? setLastSmallSlide(2)
      : setLastSmallSlide(lastSmallSlide + 3);
  };

  const handlePrevSmallSlides = () => {
    lastSmallSlide <= 2
      ? setLastSmallSlide(smallPosts.length - 1)
      : setLastSmallSlide(lastSmallSlide - 3);
  };

  const hadnlePrevSlide = () => {
    return handlePrevBigSlides(), handlePrevSmallSlides();
  };
  const hadnleNextSlide = () => {
    return handleNextBigSlides(), handleNextSmallSlides();
  };

  return (
    <Wrapper>
      <Content>
        <Header>
          <Title>happening now</Title>
          <SliderNav>
            <Arrow left onClick={hadnlePrevSlide} />
            <Arrow onClick={hadnleNextSlide} />
          </SliderNav>
        </Header>

        <Posts>
          <BigPostBlock>
            {bigPosts.map((bigPost, index) => (
              <BigPost
                display={index == lastBigSlide || lastBigSlide - index == 1}
                key={"big_post" + index}
                img={bigPost.img}
              >
                <BigPostTitle>{bigPost.title}</BigPostTitle>
                <BigPostSubtitle>{bigPost.subtitle}</BigPostSubtitle>
                <BigPostTime>{bigPost.time}</BigPostTime>
              </BigPost>
            ))}
          </BigPostBlock>
          <SmallPostBlock>
            {smallPosts.map((smallPost, index) => (
              <SmallPost
                key={"post-small " + index}
                display={
                  index == lastSmallSlide ||
                  lastSmallSlide - index == 1 ||
                  lastSmallSlide - index == 2
                }
              >
                {" "}
                <SmallPostImage img={smallPost.img} />
                <SmallPostTitle>{smallPost.title}</SmallPostTitle>
                <SmallPostTime>{smallPost.time}</SmallPostTime>
              </SmallPost>
            ))}
          </SmallPostBlock>
        </Posts>
      </Content>
    </Wrapper>
  );
};
