import React, { useState } from "react";

import PostImage1 from "../../../static/img/trending_1.png";
import PostImage2 from "../../../static/img/trending_2.png";
import PostImage3 from "../../../static/img/trending_3.png";

import {
  Wrapper,
  Content,
  Header,
  Title,
  SliderNav,
  Arrow,
  PostList,
  Post,
  PostImage,
  PostContent,
  PostTitle,
  PostSubtitle,
  PostTime,
} from "./styled";

const posts = [
  {
    title: "Dolore magna aliqua",
    subtitle:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
    img: PostImage1,
  },
  {
    title: "Morbi eleifend a libero",
    subtitle:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
    img: PostImage2,
  },
  {
    title: "Morbi eleifend a libero 1",
    subtitle:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
    img: PostImage3,
  },
  {
    title: "Dolore magna aliqua 45",
    subtitle:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
    img: PostImage1,
  },
  {
    title: "Morbi eleifend a libero 123",
    subtitle:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
    img: PostImage2,
  },
  {
    title: "Morbi eleifend a libero 12",
    subtitle:
      "Lorem ipsum dolor sit amet, ipsum labitur lucilius mel id, ad has appareat…",
    time: "2 min ago",
    img: PostImage3,
  },
];

export default () => {
  const [lastSlide, setLastSlide] = useState(2);

  const handleNextSlides = () => {
    posts.length - 1 <= lastSlide
      ? setLastSlide(2)
      : setLastSlide(lastSlide + 3);
  };

  const handlePrevSlides = () => {
    lastSlide <= 2
      ? setLastSlide(posts.length - 1)
      : setLastSlide(lastSlide - 3);
  };
  return (
    <Wrapper>
      <Content>
        <Header>
          <Title>trending</Title>
          <SliderNav>
            <Arrow left onClick={handlePrevSlides} />
            <Arrow onClick={handleNextSlides} />
          </SliderNav>
        </Header>
        <PostList>
          {posts.map((post, index) => (
            <Post
              key={"post-trending " + index}
              display={
                index == lastSlide ||
                lastSlide - index == 1 ||
                lastSlide - index == 2
              }
            >
              <PostImage img={post.img} />
              <PostContent>
                <PostTitle>{post.title}</PostTitle>
                <PostSubtitle>{post.subtitle}</PostSubtitle>
                <PostTime>{post.time}</PostTime>
              </PostContent>
            </Post>
          ))}
        </PostList>
      </Content>
    </Wrapper>
  );
};
